export default function removeAttrs(elem, attrs) {
    var i = 0;
    var length = attrs.length;
    for (; i < length; i++) {
        var attr = attrs[i];
        elem.getAttribute(attr) || elem.getAttribute(attr) === ''
            ? elem.removeAttribute(attr)
            : null;
    }
}
